<template>
    <section class="appMain">
        <section class="operationLayout">
            <button type="orange" @click="showAlertDialog(true)">()</button>
            <button type="orange" @click="showAlertDialog(false)">()</button>
            <button type="orange" @click="showConfirmDialog"></button>
            <button type="orange" @click="showInputDialog"></button>
            <button type="orange" @click="showSingleDialog"></button>
            <button type="orange" @click="showMultipleDialog"></button>
        </section>
        <BaseDialogLayout :isShow="isShowDialog" :options="dialogOptions"></BaseDialogLayout>
        <ToastLayout></ToastLayout>
    </section>
</template>


<script>
    import { BaseDialogLayout, ToastLayout } from '../../../../utils/web/Components';
    import {} from '../../../../utils/web/Utils';
    import { mapActions } from 'vuex';

    export default {
        data () {
            return {
                dialogOptions: {},
                isShowDialog: false
            };
        },
        created () {
        },
        methods: {
            ...mapActions(['showToast']),
            showAlertDialog (isOutsiteDismiss) {
                this.showDialog({
                    title: '',
                    content: '',
                    onHide: this.hideDialog,
                    onWarn: this.warn,
                    onPositive: (target) => {
                        console.log(target);
                    },
                    outsiteDismiss: isOutsiteDismiss // 
                });
            },
            showConfirmDialog () {
                let self = this;
                this.showDialog({
                    title: '',
                    content: '',
                    onHide: this.hideDialog,
                    onWarn: this.warn,
                    positiveText: '',
                    negativeText: '',
                    onPositive: (target) => {
                        console.log(target);
                        self.showToast('');
                    },
                    onNegative: (target) => {
                        console.log(target);
                        self.showToast('');
                    }
                });
            },
            showInputDialog () {
                let self = this;
                this.showDialog({
                    title: '',
                    content: '',
                    inputHint: '10~20',
                    inputMinLength: 10,
                    inputMaxLength: 20,
                    onHide: this.hideDialog,
                    onWarn: this.warn,
                    positiveText: '',
                    negativeText: '',
                    onPositive: (target, inputContent) => {
                        console.log(target);
                        self.showToast('' + inputContent);
                        // do sth.  self.hideDialog() 
                    },
                    onNegative: (target) => {
                        console.log(target);
                        self.hideDialog();
                    },
                    outsiteDismiss: false,
                    autoDismiss: false // 
                });
            },
            showSingleDialog () {
                let self = this;
                let itemList = ['A', 'B', 'C'];
                this.showDialog({
                    title: '',
                    content: '',
                    itemList: itemList,
                    onHide: this.hideDialog,
                    onWarn: this.warn,
                    positiveText: '',
                    onSingleSelected: (target, item, index) => {
                        console.log(target, item, index);
                        self.showToast('' + item + '' + (index + 1) + '');
                    }
                });
            },
            showMultipleDialog () {
                let self = this;
                let itemList = ['', '', ''];
                this.showDialog({
                    title: '',
                    content: '',
                    itemList: itemList,
                    onHide: this.hideDialog,
                    onWarn: this.warn,
                    onPositive: (target, inputContent, selectedIndexList) => {
                        console.log(target, inputContent, selectedIndexList);
                        let selectedList = [];
                        selectedIndexList.forEach((item) => {
                            selectedList.push(itemList[item]);
                        });
                        self.showToast('' + selectedList.join(''));
                    }
                });
            },
            warn (target, msg) {
                console.log(target, msg);
                this.showToast(msg);
            },
            hideDialog (target) {
                this.isShowDialog = false;
                console.log(target);
            },
            showDialog (dialogOptions) {
                this.dialogOptions = dialogOptions;
                this.isShowDialog = true;
            }
        },
        mounted () {
        },
        components: {BaseDialogLayout, ToastLayout},
        computed: {}
    };
</script>
<style lang="scss" scoped>
    @import '../../../scss/default.scss';
    @import '../../../assets/css/common/material-design.css';

    .operationLayout button {
        height: $s50;
        padding: 0 $s20;
        min-width: $s120;
        margin: $s20;
        display: inline-block;
    }
</style>


