<template>
    <section class="baseDialogLayout h100 w100 bg-default global-layout" v-if="isShow" @click.self="bodyClick">
        <section class="dialogArea bg-w shadow"
                 :style="{'margin-top':dialogAreaTop}">
            <h4 class="title" v-if="title">{{title}}</h4>
            <p class="content" v-if="content">
                <span>{{content}}</span>
            </p>
            <ul class="itemList overScroll" v-if="itemList&&itemList.length>0">
                <li class="itemListCell clickable"
                    v-for="(item,index) in itemList"
                    @click="onItemSelected(item,index)"
                    :class="[itemSelected[index + '']?'itemSelected':'']"
                >
                    <span>{{item}}</span>
                </li>
            </ul>
            <section class="inputArea pdl20 pdr20" v-else-if="isInput">
                <input class="input w100 mgb10"
                       :placeholder="inputHint"
                       :maxlength="inputMaxLength"
                       v-model="inputContent"
                />
                <p class="inputLength mgb20 text-orange">
                    <span>{{inputContent.length + '/' + inputMaxLength}}</span>
                </p>
            </section>
            <section class="btnArea" v-if="!this.onSingleSelected">
                <span v-if="positiveText" @click="invoke('onPositive')"
                      :style="{opacity:(!isInput||(isInput&&isInputContentPass)?1:0.6)}">{{positiveText}}</span>
                <span v-if="negativeText" @click="invoke('onNegative')">{{negativeText}}</span>
                <span v-if="neutralText" @click="invoke('onNeutral')">{{neutralText}}</span>
            </section>
        </section>
    </section>
</template>
<script>
    import {} from '../../utils/web/Components';
    import { TextUtils } from '../../utils/web/Utils';
    import { mapActions, mapGetters } from 'vuex';

    export default {
        props: ['options', 'isShow'],
        data () {
            return {
                // 
                title: '', // 
                content: '', // 
                // 
                itemList: [], // 
                minSelected: 1, // 
                // 
                inputHint: '', // 
                inputMinLength: 0, // 
                inputMaxLength: 10, // 
                inputContent: '', // 
                // 
                positiveText: '', // positive
                negativeText: '', // negative
                neutralText: '', // neutral
                // 
                isSelect: false, // 
                isMultiple: false, //  false
                isInput: false, // 
                isOutsiteDismiss: true, // ,
                isAutoDismiss: true, // 
                // 
                onHide: null, // 
                onWarn: null,
                onPositive: null, // 
                onNegative: null, // 
                onNeutral: null, // 
                onSingleSelected: null, // 
                // 
                target: null, // 
                // 
                dialogAreaTop: 'auto',
                itemSelected: {}
            };
        },
        created () {
            this.setConfig();
        },
        updated () {
        },
        watch: {
            isShow () {
                this.setConfig();
            },
            options () {
                this.setConfig();
            }
        },
        methods: {
            setConfig () { // 
                let option = this.options || {};
                // 
                this.title = option.title || ''; // 
                this.content = option.content || ''; // 
                // 
                this.itemList = option.itemList || []; // 
                // 
                this.inputHint = option.inputHint || ''; // 
                this.inputContent = option.inputDefaultContent || ''; // 
                this.inputMinLength = option.inputMinLength || 0; // 
                this.inputMaxLength = option.inputMaxLength || 10; // 
                // 
                this.positiveText = option.positiveText || ''; // positive
                this.negativeText = option.negativeText || ''; // negative
                this.neutralText = option.neutralText || ''; // neutral
                // 
                this.isSelect = this.itemList.length > 0; // 
                this.isInput = !!this.inputHint; // 
                this.isOutsiteDismiss = option.outsiteDismiss === undefined ? true : option.outsiteDismiss; // ;
                this.isAutoDismiss = option.autoDismiss === undefined ? true : option.autoDismiss; // 
                this.itemSelected = {};
                // 
                this.onHide = option.onHide || null; // 
                this.onWarn = option.onWarn || null; // 
                this.onPositive = option.onPositive || null; // 
                this.onNegative = option.onNegative || null; // 
                this.onNeutral = option.onNeutral || null; // 
                if (this.isSelect) { // 
                    this.onSingleSelected = option.onSingleSelected || null; // 
                    if (this.onSingleSelected) { //  onSingleSelected 
                        this.positiveText = '';
                        this.isMultiple = false;
                    } else { //  false
                        this.positiveText = this.positiveText || '';
                        this.isMultiple = true;
                        this.minSelected = option.minSelected || 1; // 
                    }
                } else {
                    this.isMultiple = false;
                }
                // 
                this.target = option.target || null; // 
                this.$nextTick(() => { // 
                    if (this.$el && this.$el.querySelector) {
                        this.dialogAreaTop = (window.document.body.offsetHeight - this.$el.querySelector('.dialogArea').offsetHeight) / 2 + 'px';
                    }
                });
            },
            onItemSelected (item, index) { // 
                if (this.isMultiple) { // 
                    this.itemSelected[index + ''] = !(this.itemSelected[index + ''] ? this.itemSelected[index + ''] : false);
                    this.itemList = JSON.parse(JSON.stringify(this.itemList));
                } else if (this.onSingleSelected) {
                    this.onSingleSelected(this.target, item, index);
                    this.autoHide();
                }
            },
            autoHide () { // 
                if (this.isAutoDismiss) {
                    this.hide();
                }
            },
            hide () { // 
                if (this.onHide) {
                    this.onHide(this.target);
                }
            },
            warn (msg) { // 
                if (this.onWarn) {
                    this.onWarn(this.target, msg);
                }
            },
            invoke (action) { // 
                let selectedIndexList = [];
                if (action === 'onPositive') { // 
                    if (this.isMultiple) { // 
                        for (let key in this.itemSelected) {
                            if (this.itemSelected[key]) {
                                selectedIndexList.push(parseInt(key));
                            }
                        }
                        if (selectedIndexList.length < this.minSelected) { // 
                            this.warn('' + this.minSelected + '');
                            return false;
                        }
                    } else if (this.isInput) { // 
                        if (!this.isInputContentPass) {
                            this.warn('' + this.inputMinLength + '~' + this.inputMaxLength + '');
                            return false;
                        }
                    }
                }
                if (this[action]) {
                    if (action === 'onPositive') {
                        this[action](this.target, this.inputContent, selectedIndexList);
                    } else {
                        this[action](this.target);
                    }
                    this.autoHide();
                } else {
                    console.log('' + action);
                    this.hide();
                }
            },
            bodyClick () {
                if (this.isOutsiteDismiss) {
                    this.hide();
                }
            }
        },
        computed: {
            isInputContentPass () { // 
                return TextUtils.checkLength(this.inputContent, this.inputMinLength, this.inputMaxLength);
            }
        },
        components: {}
    };
</script>
<style lang="scss" scoped>
    @import '../scss/default.scss';
    @import '../assets/css/common/material-design.css';

    .baseDialogLayout {
        background-color: rgba(0, 0, 0, .7);
        z-index: 997;
    }

    .dialogArea {
        padding: rem(20px);
        border-radius: rem(5px);
        margin: auto auto;
        min-width: rem(350px);
        max-width: rem(500px);
    }

    .title {
        margin-bottom: rem(20px);
        font-size: rem(23px);
    }

    .content {
        font-size: rem(20px);
        padding: rem(10px) rem(20px) rem(5px);
        margin-bottom: rem(10px);
    }

    .itemList {
        margin-bottom: rem(10px);
        max-height: rem(260px);
    }

    .itemListCell {
        padding: 0 rem(20px);
        height: rem(50px);
        line-height: rem(50px);
        cursor: pointer;
        border-bottom: 1px solid #f1f1f1;
    }

    .itemListCell.itemSelected {
        color: white;
        background-color: $orange;
        opacity: 0.85;
    }

    .input {
        height: rem(60px);
        line-height: rem(60px);
        padding: 0 rem(5px);
        border-bottom: 1px solid $orange;
        color: #4b4b57;
    }

    .inputLength {
        text-align: right;
        font-size: rem(16px);
    }

    .btnArea {
        height: rem(40px);
        color: $orange;
    }

    .btnArea span {
        float: right;
        height: rem(40px);
        line-height: rem(40px);
        width: rem(60px);
        margin: 0 rem(10px);
        text-align: center;
        cursor: pointer;
    }

</style>


